<template>
  <view>
    <image :src="music.hts_MVPIC" class="albumpic"></image>
  </view>
</template>

<script>
import {videoItem} from "./server";

export default {
  data() {
    return {
      burl:'https://img2.kuwo.cn/star/albumcover/',
      music:{}
    };
  },
  onLoad() {
    this.music = uni.getStorageSync('music')
    console.log(uni.getStorageSync('music'));
    videoItem({
      rid:this.music.DC_TARGETID
    }).then(res=>{
      console.log(res);
    })
  },
  methods: {},
};
</script>

<style scoped lang="scss">
// 动画
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.albumpic{
  width: 600rpx;
